<template>
  <div class="bg">
    <div v-if="userInfo" class="user_info">
      <div class="left">
        <p>
          <img :src="imgUrl+userInfo.photo + '?'+ Math.random()" alt />
        </p>
        <p>
          <img src="../../assets/img/vip/icon-1.png" alt />
          {{userInfo.memberName}}
        </p>
      </div>
      <div class="right">
        <p class="p1">{{userInfo.phone}}</p>
        <p class="p2">
          账号认证：
          <router-link
            to="/user/certify-index"
          >{{userInfo.auditingType == 0 ? '未验证' : userInfo.auditingType == 1? '个人认证' : userInfo.auditingType == 2 ? '个人/企业' : userInfo.auditingType == 3 ? '政府机构' : '未知身份'}}</router-link>
        </p>
        <p class="p2">项目上限：{{userInfo.project.maxCount}}个 已发布：{{userInfo.project.nowCount}}个</p>
        <p class="p2">资金上限：{{userInfo.money.maxCount}}个 已发布：{{userInfo.money.nowCount}}个</p>
        <p class="p2">
          <Button @click="logout" type="warning" style="font-size:14px;margin-top:10px;">退出登录</Button>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, Notice } from "iview";
import { messages, imgUrl } from "../../utils";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    [Button.name]: Button,
    [Notice.name]: Notice
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      imgUrl: imgUrl
    };
  },
  methods: {
    ...mapActions("app", ["Logout"]),
    logout() {
      this.Logout().then(() => {
        messages.success(`您已成功退出登录，感谢您的使用`);
        this.$router.push("/index");
      });
    }
  }
};
</script>


